import React from 'react'

const Participants = ({ participants, remoteSrc }) => {
		console.log(participants)
		let userArray = Object.keys(participants)
		let username = userArray[0]
		return (
		<div key="ParticipantList" className="Participantlist">
			<div key={username} className="ParticipantContainer">
			<p className="nickname" 
			style={{ 
				textAlign: 'center',
				marginBottom: '-0.5em',
				fontSize: "1.6em",
				textTransform: "uppercase"
				}}>{username}</p>
			<p 
				className="userMessage" 
				style={{ 
					textAlign: 'center',
					fontWeight: "300",
					fontSize: "1.5em",
				}}>{participants[username]}
			</p>
	</div></div>
	)
}


