import { connect } from 'react-redux'
import Chat from './Chat'

// Redux selector to remove own nickname from participant list
function ownNickRemover(userlist, owNickname) {
	delete userlist[owNickname]
	return userlist
}


const mapStateToProps = (state) => {
	console.log(state.NicknameReducer.nickname)
	return {
		// ownID: state.NicknameReducer.socketID,
		ownNickname: state.NicknameReducer.nickname,
		participants: ownNickRemover(state.ChatReducer, state.NicknameReducer.nickname),
		localSrc: state.VideoReducer.localSrc,
		remoteSrc: state.VideoReducer.remoteSrc
	}
}

// participants format:
// // {
// 	tuomo: "hello world",
// 	veli: "hello back"
// }

const ChatContainer = connect(
	mapStateToProps
	)(Chat)

export default ChatContainer


