// Selector for inbound full state parsing
function userParser(userArray) {
	console.log(userArray)
	let userObject = {}
	userArray.forEach(user => userObject[user] = '')
	return userObject
}

function ChatReducer(state = {}, action) {

	switch(action.type) {

		case 'INBOUND_MESSAGE':
			return {...state, [action.nickname]: action.message}

		case 'FULL_STATE':
			return userParser(action.users.users)

		// case 'INBOUND_MESSAGE':
		// 	return  {...state,
		// 		[action.userID]: {
		// 			nickname: action.nickname,
		// 			socketId: action.userID,
		// 			message: action.message
		// 		}
		// 	}


		// case 'FULL_STATE':
		// 	return Object.assign({},
		// 		action.users
		// 		)



		default:
			return state
}}

export default ChatReducer


