import { Socket } from 'phoenix'
import request from 'superagent'

export default function channelsMiddleware(url) {
    
    return store => {
        // Set up connection here
        let activeConnection
        let channel
        function signInChannel(nickname, token) {
            let socket = new Socket(url, {params: {nickname: nickname, token: token}})
            socket.connect()
            channel = socket.channel("call:hello", {})
            channel.join()
                .receive("ok", () => { 
                    activeConnection = true
                    console.log("Successfully joined call channel")
                    console.log("dispatching action set_nickname")
                    store.dispatch({
                        type: 'set_nickname',
                        nickname: nickname
                    })
                })
                .receive("error", () => { console.log("Unable to join") })

            channel.on("webrtc_message", payload => {
                let message = JSON.parse(payload.body)
                let sender = payload.sender
                console.log(message)
                if (message.sdp && sender !== store.getState().NicknameReducer.nickname) {
                    if (message.sdp.type === "offer") {
                        console.log("Got remote offer from: " + sender)
                        gotRemoteDescription(message)
                    }
                    if (message.sdp.type === "answer") {
                        console.log("Got remote answer from: " + sender)
                    }
                } else { // will only add remote candidate to local pc (peerconnection)
                    if (sender !== store.getState().NicknameReducer.nickname) {
                        gotRemoteIceCandidate(message)
                    }
                }
            })

            channel.on("presence_state", users => store.dispatch({type: 'FULL_STATE', users: users}))

            channel.on("phoenix_message", message => console.log(message))

            channel.on("typing_message", body => {
                console.log(body)
                store.dispatch({
                    type: 'INBOUND_MESSAGE',
                    nickname: body.nickname,
                    message: JSON.parse(body.message)
                })})

            channel.on("SEND_OFFER", () => store.dispatch({type: 'SEND_OFFER'}))

            channel.on(/.*/, message => console.log(message))
    
            channel.onClose(() => {
                activeConnection = false
                console.log("Channel closed")
            })
        }

        function registerNickname(nickname) {
            console.log(nickname)

            // superagent:
            request
                // .post('http://localhost:4000/room')
                .post('https://atm-chat.herokuapp.com/room')
                .set('Content-Type', 'application/json')
                .send({nickname: nickname})
                .end((err, res) => {
                    console.log(res.body)
                    // if response gives back username
                    if (res.body.nickname) {
                        console.log(`activating signinchannel with nick: ${res.body.nickname}`)
                        signInChannel(res.body.nickname, res.body.token)
                    } 
                    if (res.body.error_nickname) {
                        console.log("Nickname already taken")
                    }
                    if (res.body.error_room) {
                        console.log("Room already full")
                        console.log(res.body.nicknames)
                    } else {
                        console.log("some other HTTP response")
                        console.log(res)
                    }

                })
        }


        
        // Added for Phoenix Channels test
        // const room = socket.channel("call:tuomo")
        // room.join()
        // End of test
        function joinRoom(socket) {
            const room = socket.channel("call:only_room")
            room.join()
        }

        function typingMessageSend(message) {
            channel.push("typing_message", { 
                message: JSON.stringify(message)  
            })
        }

        let offerOptions = {
            offerToReceiveAudio: 0,
            offerToReceiveVideo: 1
        }
        // let remoteVideo

        var conf = {iceServers: [{url: 'stun:stun.l.google.com:19302'}]}
        const pc = new RTCPeerConnection(conf)
        console.log("Created local peer connection")

        pc.onicecandidate = event => {
            console.log("onicecandidate")
            if (event.candidate) {
                console.log("Local ICE candidate: \n" + event.candidate.candidate);
                channel.push("webrtc_message", {body: JSON.stringify({
                            "candidate": event.candidate
                        })});
                } else {
                    console.log("event.candidate is empty")
                }
            }

        pc.oniceconnectionstatechange = () => {
            if (pc.iceConnectionState === 'disconnected') {
                store.dispatch({type: 'PEER_DISCONNECT'})
            }
        }

        pc.onaddstream = event => { 
            // Dispatch an action with the stream, to be handled by VideoReducer
            store.dispatch({type: 'SET_REMOTE_STREAM', stream: window.URL.createObjectURL(event.stream)})
            // remoteVideo.src = window.URL.createObjectURL(event.stream)
            console.log("Received remote stream")
        }

        function gotRemoteDescription(description) {
            console.log("Offer from remotePeerConnection: \n" + description.sdp);
            console.log("creating answer")
            pc.setRemoteDescription(new RTCSessionDescription(description.sdp))
              .then(() => pc.createAnswer(offerOptions))
              .then(gotLocalDescription)
              .catch(handleError)
        }
    
        function gotLocalDescription(description) {
            pc.setLocalDescription(description, () => {
                console.log("sending webrtc message")
                channel.push("webrtc_message", { body: JSON.stringify({
                    "sdp": description // pc.localDescription
                })});
            }, handleError)
            console.log("Answer description from localPeerConnection: \n" + description.sdp)
        }
    
        function gotRemoteIceCandidate(event) {
            if (event.candidate) {
                console.log("gotRemoteIceCandidate triggered")
                pc.addIceCandidate(new RTCIceCandidate(event.candidate))
                    .catch(handleError)
                console.log("Remote ICE candidate: \n " + event.candidate.candidate);
            }
        }

        function sendOffer() {
            
            // Trust that when a request comes client has already determined user wants to be a host
            if (activeConnection) {
                console.log("sendOffer called")
                pc.createOffer(offerOptions)
                    .then(offer => pc.setLocalDescription(offer))
                    .then(() => {
                        console.log("Offer from localPeerConnection")
                        channel.push("webrtc_message", { 
                            body: JSON.stringify({ "sdp": pc.localDescription })  
                        })})
                    .catch(handleError)

            } else { console.log("connection to Phoenix not established yet") }
        }
    
        function handleError(e) { console.log("handleError: " + e) }

        // var localStream
        var localStream
        function requestVideo() {
            const videoOptions = { 
                audio: false,
                video: {
                    facingMode: "user",
                    width: 60,
                    height: 60,
                    frameRate: { 
                        ideal: 10, 
                        max: 10
                    }
            }}
            console.log("Requesting access to local video and audio");
            navigator.mediaDevices.getUserMedia(videoOptions)
                .then(stream => {
                    console.log("Received local video & audio stream")
                    localStream = stream
                    // Dispatch an action with the stream data for VideoReducer to handle
                    store.dispatch({type: 'SET_LOCAL_STREAM', stream: URL.createObjectURL(stream)})
                })
                .then(() => {
                    console.log("added stream:")
                    console.log(localStream)
                    pc.addStream(localStream)
                })
                .catch(handleError)
        }



        return next => action => {
            // Set up actions here
            switch (action.type) {

                case 'TYPING_MESSAGE_SEND':
                    typingMessageSend(action.message)
                    break

                case 'REGISTER_NICKNAME':
                    registerNickname(action.nickname)
                    break

                case "JOIN_ROOM":
                    joinRoom(action.socket)
                    break
                
                case 'REQUEST_VIDEO':
                    requestVideo()
                    break

                case 'SEND_OFFER':
                    sendOffer()
                    break
        
                case 'WEBRTC_MESSAGE':
                    console.log("WEBRTC_MESSAGE")
                    break

                // case 'MAKE_CALL':
                //     call()
                //     break
        
                default:
                    break
            }

            return next(action)
        }
    }
}


